<template>
    <div>
        <el-form ref="form" :rules="rules" :aria-disabled="form.isPub" :model="form" label-width="80px">
            <el-form-item prop="productName" label="">
                <el-input v-model="form.productName"></el-input>
            </el-form-item>
            <el-form-item prop="pinpai" label="">
                <el-input v-model="form.pinpai"></el-input>
            </el-form-item>
            <el-form-item prop="xinghao" label="">
                <el-input v-model="form.xinghao"></el-input>
            </el-form-item>
            <el-form-item prop="sumCount" label="">
                <el-input v-model="form.sumCount"></el-input>
            </el-form-item>
            <el-form-item prop="smallCount" label="">
                <el-input v-model="form.smallCount"></el-input>
            </el-form-item>
            <el-form-item prop="price" label="">
                <el-input v-model="form.price"></el-input>
            </el-form-item>
            <el-form-item prop="unit" label="">
                <el-input v-model="form.unit"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="updateData"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
import qs from 'qs'
export default {
    data () {
        return {
            goodsId: this.$route.params.goodsId,
            form: {
                Id: '',
                city: '',
                createTime: '',
                editTime: '',
                endPubCount: 0, // 
                endTodayPubCount: 0, // 
                informationType: '', // PRODUCT  NEWS 
                isPub: false, //  
                maxPubCount: 1000, // 
                pinpai: "", // 
                price: "1111", // 
                productName: "", // 
                pubInterval: 60, // s
                pub_startTime: "2018-09-03 00:00:00", // 
                smallCount: "11111", // 
                sumCount: "111111", // 
                unit: "", // 
                xinghao: "11" // 
            },
            rules: {
                productName: [
                  { required: true, message: '', trigger: 'blur' }
                ],
                pinpai: [
                  { required: true, message: '', trigger: 'blur' }
                ],
                xinghao: [
                  { required: true, message: '', trigger: 'blur' }
                ],
                sumCount: [
                  { required: true, message: '', trigger: 'blur' }
                ],
                smallCount: [
                  { required: true, message: '', trigger: 'blur' }
                ],
                price: [
                  { required: true, message: '', trigger: 'blur' }
                ],
                unit: [
                  { required: true, message: '', trigger: 'blur' }
                ]
            }
        }
    },
    mounted: function () {
        this.init()
    },
    methods: {
        init() {
            this.getData()
        },
        getData () {
            let params = {
                productId: this.goodsId
            }
            this.$store.dispatch('getProductById', params).then(res => {
                if (res.data.code === 1) {
                    this.form = res.data.detail.product
                } else {
                    this.$message.error('')
                }
            }).catch(err => {
                console.log(err)
                this.$message.error('')
            })
        },
        updateData () { // 
            this.$refs.form.validate((valid) => {
                if (valid) {
                    let params = {
                        params: JSON.stringify(this.form)
                    }
                    this.$store.dispatch('saveProduct', qs.stringify(params)).then(res => {
                        if (res.data.code === 1) {
                            debugger
                            this.$message.success('')
                        } else {
                            this.$message.error('')
                        }
                    }).catch(err => {
                        this.$message.error('')
                    })
                } else {
                    this.$message.warning('')
                }
            })
        }
    }
}
</script>
<style lang="less" scoped>

</style>



